<template>
  <div>
    Card

    <div v-if="loading">loading card...</div>
    <div v-else>
      <div>cid : {{cid2}}</div>
    </div>
  </div>

</template>
<script>
  export default {

    data() {
      return {
        cid: 0,
        loading : false
      }
    },
    computed: {
      cid2 : function(){
        return this.cid
      }
    },
    watch: {

      //    . =>  '$route' :  O /  $route (){} X

      '$route': {

        //this.cid = this.$route.params.cid

        //this.fetchData()

        //       =>  
        handler: 'fetchData',
        //    => created 
        immediate : true
      }
    },
    
    //created() {

    //  this.fetchData()
   
    //},
    methods: {
      fetchData() {
        this.loading = true
        setTimeout(() => {
          this.loading = false,
          this.cid = this.$route.params.cid
        }, 500)
      }
    }
  }
</script>
